@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@100&display=swap);
/* Hide scrollbar for Chrome, Safari and Opera */
html::-webkit-scrollbar {
  display: none;
}

html {
  -ms-overflow-style: none; /* IE and Edge */
  scrollbar-width: none; /* Firefox */
  height: 100%;
}
body {
  margin: 0;
  font-family: 'Noto Sans KR', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100%;
}

#root {
  height: 100%;
}

.faq-card{margin:2rem auto 2rem auto;min-width:450px;max-width:450px;border:none}.faq-card .faq-content{line-height:1.6rem}

#faq{max-width:1000px;margin-top:3rem;margin-left:auto;margin-right:auto}

.href-link{color:#000;margin:1rem}

#introduction{padding:5rem 0rem 5rem 0rem}#introduction .description{text-align:center;max-width:800px;margin-left:auto;margin-right:auto}

#main #title{text-align:center}#main .description{max-width:800px;margin-left:auto;margin-right:auto;margin-top:3rem;padding:2rem}#main .parallax{background-image:url(/Re-born/static/media/main.9b7c6f08.jpg);min-height:500px;width:80%;margin:5rem 10% 3rem 10%;background-attachment:fixed;background-position:center;background-repeat:no-repeat;background-size:cover}

#app{width:100%;height:100%;padding-top:3rem}#app #nav-bar{max-width:800px;margin-left:auto;margin-right:auto}#app .brand-text{text-decoration:none;color:#000}#app footer{padding:5rem 0rem 5rem 0rem;text-align:center}

